import React, { Component } from "react";
import "./App.css";
import { isMobile } from "react-device-detect";
import minbox from "./assets/SVG/mintbox_logo.svg";

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      load: true,
    };
  }

  componentDidMount() {
    this.setState({
      load: false,
    });
  }

  render() {
    if (isMobile) {
      return (
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            minHeight:"90vh",
            alignItems:"center",
            justifyContent:"center"
          }}
        >
          <div>
            <img src={minbox} height="100px" width="100%" />
          </div>
          <h1 style={{ textAlign: "center" }}>HEY! WE'RE BUILDING OUR WEB APP</h1>
        </div>
      );
    } else {
      return (
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            minHeight:"90vh",
            alignItems:"center",
            justifyContent:"center"
          }}
        >
          <div>
            <img src={minbox} height="100px" width="100%" />
          </div>
          <h1 style={{ textAlign: "center" }}>HEY! WE'RE BUILDING OUR WEBSITE</h1>
        </div>
      );
    }
  }
}

export default App;
